#clip-tech {
  width: 0;
  height: 0;
  path {
    fill: #000; } }

.mask {
  display: block; }

.skills {
  @include row();
  &__img {
   	background:	linear-gradient(45deg, hsla(340, 100%, 55%, 1) 0%, hsla(340, 100%, 55%, 0) 70%),linear-gradient(135deg, hsla(225, 95%, 50%, 1) 10%, hsla(225, 95%, 50%, 0) 80%),linear-gradient(225deg, hsla(140, 90%, 50%, 1) 10%, hsla(140, 90%, 50%, 0) 80%),linear-gradient(315deg, hsla(35, 95%, 55%, 1) 100%, hsla(35, 95%, 55%, 0) 70%);
    min-width: 300px;
    max-width: 500px; }
  &__check-list {
    flex: 0 0 auto;
    align-self: center;
    font-size: 18px; }
  &__view {
    min-width: 250px;
    align-self: center;
    margin: auto;
    text-align: center; } }


.skills-list {
  @include col(1);
  margin-top: $l-g;
  &__item {
    backface-visibility: hidden;
    background-color: #fff;
    @include mc();
    @include col(1);
    @include r(md) {
      @include col(1/3); } } }


.check-list {
  @extend %ul-l;
  &__item {
    margin-top: 5px;
    padding-left: 40px;
    position: relative;
    &:first-child {
      margin-top: 0; }
    .svg-icon {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      color: $c-brand;
      display: inline-block;
      vertical-align: middle; } } }


.works {
  &__list {
    @include row(); }
  &__item {
    @include mc();
    @include col(1);
    @include r(sm) {
      @include col(1/2); }
    @include r(md) {
      @include col(1/3); } }
  &__cart {
    height: 250px;
    overflow: hidden;
    // transition: all .3s
    // &:hover
    //   transform: scale(1.1)
 } }    //   box-shadow: 0 0 10px rgba(#000, .3)

.works-flt {
  @extend %ul-l;
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
  &__item {
    line-height: 3;
    display: inline-block; } }

.my-modal {
  position: fixed;
  width: 100%;
  height: 10%;
  z-index: 1000;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 60px 10vw 10vh 10vw;
  overflow-y: auto;
  // background-color: rgba(#000, .7)
  background:	linear-gradient(45deg, rgba(8, 8, 21, 0.98) 30%, #000315); }

.work-about {
  width: 100%;
  height: 100%;
  // padding: 60px 20px 20px
  color: #fff;
  // overflow-y: scroll
  &__img {
    position: relative;
    height: 100%;
    @include col(1/2);
 }    // width: 50%
  &__img-wrap {
    max-height: calc(100vh - 10vh - 60px);
    overflow-y: auto;

    img {
      width: 100%; } } }
.close-modal {
  background-color: red;
  width: 40px;
  height: 40px; }

body.lock {
  overflow: hidden; }
